.section-group {
  background-image: url("../img/section-group__bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 14.6875rem 0 8.4375rem;
}
.main {
  color: #ffffff;
  padding: 0 0 20rem 0;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 35.625rem;
    height: 35.625rem;
    background: radial-gradient(50% 50% at 50% 50%, #F3D545 0%, rgba(255, 255, 255, 0.00) 100%);
    filter: blur(6.25rem);
    border-radius: 50%;
  }
  &__inner {
    position: relative;
  }
  &__wrap {
    position: relative;
    z-index: 2;
    .main__img {
      display: none;
    }
  }
  h1 {
    font-size: 6.25rem;
    line-height: 100%;
    font-weight: 300;
    text-transform: lowercase;
    span {
      font-weight: 700;
      color: #FDC32E;
    }
  }
  &__subtitle {
    font-size: 4.5rem;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: -1.536px;
    text-transform: lowercase;
    margin-top: 1.25rem;
  }
  &__text {
    position: relative;
    font-size: 1.875rem;
    line-height: 130%;
    font-weight: 700;
    padding-left: 1.6875rem;
    margin-top: 3.125rem;
    &:after {
      content: '';
      position: absolute;
      top: 0.75rem;
      left: 0;
      width: 0.6875rem;
      height: 0.6875rem;
      background: #FFF;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    margin-top: 3.125rem;
  }
  &__btn {
    width: 17.25rem;
  }
  &__info {
    position: relative;
    margin-left: 2rem;
    p {
      font-size: 1.25rem;
      line-height: 130%;
      margin: 0;
      strong {
        font-weight: 700;
      }
    }
    i {
      position: absolute;
      top: calc(100% + 0.625rem);
      left: -2rem;
      width: 7.5rem;
    }
    i img {
      width: 100%;
    }
    i img:nth-child(2) {
      display: none;
    }
  }
  &__img {
    position: absolute;
    top: 4rem;
    right: 0;
    width: 60.625rem;
    img {
      width: 100%;
    }
  }
}
.advantages {
  color: #ffffff;
  padding: 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 40%;
    margin-right: 2rem;
  }
  &__right {
    width: 60%;
    .advantages__text {
      display: none;
    }
    .advantages__btn {
      display: none !important;
    }
  }
  &__desc {
    max-width: 35rem;
  }
  h3 {
    font-size: 2.5rem;
    line-height: 120%;
    color: #ffffff;
    letter-spacing: -2px;
    span {
      color: #FDC32E;
      font-weight: 700;
    }
  }
  .subtitle {
    max-width: 22.5rem;
    padding-bottom: 0.625rem;
    margin-top: 1.875rem;
    &:after {
      background: #ffffff;
    }
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: calc(100% + 2.875rem);
      height: 0.0625rem;
      background: #D6D6D6;
    }
  }
  &__text {
    max-width: 18rem;
    font-size: 1.125rem;
    line-height: 130%;
    margin-top: 3.125rem;
  }
  &__btn {
    width: 17.25rem;
    margin-top: 3.125rem;
  }
  &__cards {
    max-width: 58.125rem;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.875rem / 2));
    gap: 2.5rem 1.875rem;
    margin-left: auto;
  }
}
.advantage-card {
  min-height: 16.5625rem;
  background-repeat: no-repeat;
  background-position: top right;
  background-size: contain;
  background-color: #ffffff;
  border-radius: 1.25rem;
  padding: 9.375rem 1.875rem 1.875rem;
  &__title {
    max-width: 23.125rem;
    font-size: 1.25rem;
    line-height: 130%;
    color: #000;
    strong {
      font-weight: 700;
      color: #FFB800;
      text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.10);
    }
  }
}
.products {
  padding: 6.25rem 0;
  h2 {
    max-width: 38.125rem;
    span {
      font-weight: 700;
      color: #DB5200;
    }
  }
  .subtitle {
    display: inline-block;
    margin-top: 3.125rem;
    &:after {
      background: #000000;
    }
    &:before {
      content: '';
      position: absolute;
      top: -1rem;
      right: 0;
      width: calc(100% + 2.875rem);
      height: 0.0625rem;
      background: #D6D6D6;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.875rem / 2));
    grid-auto-rows: 1fr;
    gap: 1.875rem;
    margin-top: 4.375rem;
  }
  &__img {
    position: absolute;
    top: -22rem;
    left: 0;
    width: 29.6875rem;
  }
}
.product-card {
  position: relative;
  background: radial-gradient(86.24% 72.09% at 87.55% 35.14%, #FFF 0%, rgba(255, 255, 255, 0.00) 70%), #F28D19;
  border: 0.0625rem solid #FFF;
  border-radius: 1.25rem;
  padding: 2.5rem 3.125rem 1.25rem;
  &__desc {
    position: relative;
    z-index: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__title {
    font-size: 1.875rem;
    line-height: 130%;
    font-weight: 600;
    text-transform: uppercase;
    color: #FFF;
  }
  ul {
    max-width: 18.75rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
    margin-top: 1.25rem;
    margin-bottom: auto;
    li {
      position: relative;
      font-size: 1.25rem;
      line-height: 130%;
      color: #FFF;
      border-bottom: 0.0625rem solid #ffffff;
      padding-left: 1.4375rem;
      padding-bottom: 0.3125rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.5rem;
        left: 0;
        width: 0.5rem;
        height: 0.5rem;
        background: #ffffff;
        border-radius: 50%;
      }
    }
  }
  &__btn {
    width: 17.25rem;
    margin-top: 2rem;
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 28.125rem;
    img:nth-child(2) {
      display: none;
    }
  }
}




@media screen and (max-width: 1600px) {
  .main {
    padding-bottom: 16rem;
    &__img {
      width: 50rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  .main {
    h1 {
      font-size: 5rem;
    }
    &__subtitle {
      font-size: 3.75rem;
    }
    &__text {
      font-size: 1.5rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .main {
    h1 {
      font-size: 3.75rem;
    }
    &__subtitle {
      font-size: 2.5rem;
      margin-top: 1rem;
    }
    &__text {
      font-size: 1.25rem;
      margin-top: 2rem;
      &:after {
        top: 0.375rem;
      }
    }
    &__group {
      margin-top: 2rem;
    }
    &__info {
      margin-left: 1.25rem;
    }
    &__img {
      top: 1.25rem;
      width: 40rem;
    }
  }
  .advantages {
    h3 {
      font-size: 2rem;
    }
    &__text {
      font-size: 1rem;
      margin-top: 2rem;
    }
    &__btn {
      margin-top: 2rem;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
      gap: 1.5rem;
    }
  }
  .advantage-card {
    min-height: unset;
    padding: 7.5rem 1.25rem 1.25rem;
    &__title {
      font-size: 1rem;
    }
  }
  .products {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
      gap: 1.5rem;
      margin-top: 3.125rem;
    }
    &__img {
      top: -18rem;
      width: 24rem;
    }
  }
  .product-card {
    padding: 2rem;
    &__title {
      font-size: 1.5rem;
    }
    ul {
      li {
        font-size: 1.125rem;
        &:after {
          top: 0.4375rem;
        }
      }
    }
    &__img {
      max-width: 21.875rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .main {
    padding-bottom: 8.75rem;
    &__btn {
      width: 13.75rem;
    }
    &__img {
      width: 30rem;
    }
  }
  .advantages {
    .subtitle {
      &:before {
        width: calc(100% + 1.5rem);
      }
    }
  }
  .products {
    .subtitle {
      &:before {
        width: calc(100% + 1.5rem);
      }
    }
  }
}

@media screen and (max-width: 1023px) {
  .section-group {
    padding: 8rem 0 6.25rem 0;
  }
  .main {
    h1 {
      font-size: 2.5rem;
    }
    &__subtitle {
      font-size: 1.5rem;
    }
    &__text {
      padding-left: 1rem;
      &:after {
        width: 0.5rem;
        height: 0.5rem;
      }
    }
    &__info {
      p {
        font-size: 1rem;
      }
      i {
        width: 5rem;
      }
    }
    &__img {
      width: 24rem;
    }
  }
  .advantages {
    h3 {
      font-size: 1.75rem;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1rem / 2));
      gap: 1rem;
    }
  }
  .advantage-card {
    padding-top: 6.25rem;
    &__title {
      font-size: 0.875rem;
    }
  }
  .products {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 1rem / 2));
      gap: 1rem;
      margin-top: 2rem;
    }
    &__img {
      top: -10rem;
      width: 16rem;
    }
  }
  .product-card {
    padding: 1.5rem;
    &__title {
      font-size: 1.25rem;
    }
    ul {
      max-width: 100%;
      li {
        font-size: 1rem;
        &:after {
          top: 0.375rem;
        }
      }
    }
    &__img {
      max-width: 15.625rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .section-group {
    padding: 6.25rem 0 3.75rem;
  }
  .main {
    padding-bottom: 3.125rem;
    &__wrap {
      .main__img {
        display: block;
      }
    }
    &__group {
      margin-top: 3.125rem;
    }
    &__img {
      position: static;
      width: 100%;
      max-width: 25rem;
      display: none;
      margin: 1.5rem auto 0;
    }
  }
  .advantages {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 2.5rem 0;
    }
    &__right {
      width: 100%;
      .advantages__text {
        display: block;
      }
    }
    &__desc {
      max-width: 100%;
    }
    &__cards {
      max-width: 100%;
    }
    &__text {
      display: none;
    }
    .advantages__btn {
      display: none !important;
    }
    &__right {
      .advantages__btn {
        display: flex !important;
      }
    }
  }
  .products {
    padding-bottom: 3.125rem;
    &__img {
      top: -4rem;
      width: 10rem;
    }
  }
  .product-card {
    background: radial-gradient(69.12% 90.5% at 88.08% 76.4%, #FFF 0%, rgba(255, 255, 255, 0.00) 100%), #F28D19;
    &__btn {
      width: 100%;
    }
    &__img {
      max-width: 12.5rem;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &:after {
      left: 0;
      transform: translate(0, -50%);
      width: 10rem;
      height: 10rem;
      filter: blur(3.125rem);
    }
    h1 {
      font-size: 2rem;
    }
    &__subtitle {
      margin-top: 0.3125rem;
    }
    &__text {
      margin-top: 1.25rem;
    }
  }
  .advantages {
    .subtitle {
      max-width: 100%;
      margin-top: 1rem;
      &:before {
        right: -1.5rem;
        width: calc(100% + 3rem);
      }
    }
  }
  .advantage-card {
    padding: 4rem 0.75rem 1.875rem;
    &__title {
      font-size: 0.75rem;
    }
  }
  .products {
    .subtitle {
      margin-top: 2rem;
    }
  }
  .product-card {
    padding: 1.25rem 1.25rem 2rem;
    &__title {
      font-size: 1rem;
    }
    ul {
      li {
        font-size: 0.875rem;
        &:after {
          top: 0.3125rem;
        }
      }
    }
  }
}

@media screen and (max-width: 499px) {
  .main {
    &__group {
      display: block;
    }
    &__info {
      padding-left: 4.0625rem;
      margin: 1rem 0 0 0;
      i {
        top: unset;
        left: 0;
        bottom: 0;
        width: 3.75rem;
      }
      i img:nth-child(1) {
        display: none;
      }
      i img:nth-child(2) {
        display: block;
      }
    }
  }
  .advantages {
    .subtitle {
      &:before {
        right: -1rem;
        width: calc(100% + 2rem);
      }
    }
    &__btn {
      width: 100%;
    }
  }
  .products {
    .subtitle {
      &:before {
        right: -1rem;
        width: calc(100% + 2rem);
      }
    }
    &__cards {
      grid-template-columns: 100%;
      grid-auto-rows: unset;
    }
  }
  .product-card {
    &__title {
      font-size: 1.125rem;
    }
    &__btn {
      width: 16.6875rem;
    }
  }
}

@media screen and (max-width: 399px) {
  .main {
    &__btn {
      width: 100%;
    }
  }
}